import { styled } from "@mui/material/styles";
import { Box, Grid, Card, Typography } from "@mui/material";

export const MoviesContainer = styled(Box)({
    backgroundColor: "#fff",  //  
    color: "black",           //    
    minHeight: "100vh",
    padding: "20px",
});

export const MovieCard = styled(Card)({
    backgroundColor: "#fff",  //     ()
    borderRadius: "10px",
    boxShadow: "0 4px 10px rgba(0, 0, 0, 0.1)",  //    
    transition: "transform 0.3s, box-shadow 0.3s",  //    hover
    '&:hover': {
        transform: "scale(1.05)",  //     
        boxShadow: "0 6px 15px rgba(0, 0, 0, 0.15)",  //    hover
    },
});

export const MovieTitle = styled(Typography)({
    fontWeight: 600,
    color: "#000000",
});

export const MovieOverview = styled(Typography)({
    color: "#555",  //     ,    
});
